.card{
    width: 10rem;
    height:15rem;
    background-color: #fdf701;
    display: flex;
    flex-flow: column;
    align-items: center;
}



.card_img{
    width:75%;
    height:7rem;
    padding:20px 0;
}

a{
    color:inherit;
    text-decoration: none;
}

@media(min-width:600px){
    .card{
        width: 15rem;
    }
}