.Toolbar{
    height:5rem;
    width: 85%;
    position: fixed;
    top:1rem;
    left:7.5%;
    border-radius: 25px;
    display:flex;
    justify-content:space-around ;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index:90;
    background-color: rgb(101, 34, 139);
    color:white;
    opacity:0;
    
}

.logo{
    font-weight:900;
    font-size: 2rem;
    color: chartreuse;
}
.nav_items{
    display:flex;
}
.nav_item{
    padding: 0 20px;
}
.nav_item a:link{
    color:white ;
    text-decoration: none;
}

@media(min-width:600px){
    .Toolbar{
        opacity:1;
        animation-name: Appear;
        animation-duration: 1s;
    }
}

@keyframes Appear{
    0%{
        opacity:0;
        transform: scale(0.7);
    }
    75%{
        opacity:0.5;
        transform: scale(1.1);
    }
    100%{
        opacity: 1;
    }
}