.DrawerToggle{
    width:50px;
    height:3rem;
    display:flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: center;
    padding:10px 0;
    box-sizing: border-box;
    background-color: rgb(101, 34, 139);
    position: fixed;
    top:5%;
    left:7.5%;
    z-index:90;
    border-radius: 50px;
}

.DrawerToggle div{
    width:90%;
    height:3px;
    background-color: white;
}

@media(min-width:600px){
    .DrawerToggle{
        opacity: 0;
    }
}