.home{
    background-color: rgb(32, 30, 30);
    height:100vh;
    background-size: 100%;
    background-repeat: no-repeat;
}
.head{
    position:absolute;
    top:45%;
    left:50%;
    transform: translate(-50%,-50%);
    text-align: center;
    color: #eb0101;
}
.head_main{
    font-size: 3rem;
    animation-name: MoveInTop;
    animation-duration: 2s;
}
.head_sub{
    font-size: 2rem;
    animation-name: Blink;
    animation-delay: 1.5s;
    animation-fill-mode: backwards;
    animation-duration: 3s;
}

@keyframes Blink{
    0% {
        opacity: 0; }
      50% {
        opacity: 0.75; }
      75% {
        opacity: 0; }
      100% {
        opacity: 1; }
}

@keyframes MoveInTop {
    0% {
      opacity: 0;
      transform: translateY(-50px); }
    100% {
      opacity: 1; } }