.participant{
	background-color: #15eace;
	width:85%;
	color: black;
	margin: 9px;
}

.participant_data{
	display: flex;
	align-items: center;
	justify-content: space-around;
	padding: 10px 5px;
}

.remove{
	width:20px;
	height: 20px;
	background-color: red;
	border-radius: 50px;
	display: flex;
	flex-flow: column;
	align-items: center;
	justify-content: space-around;
	cursor: pointer;
}

.remove div{
	width: 90%;
	height: 3px;
	background-color: white;
}