.SideDrawer{
    background-color: rgb(101, 34, 139);
    color:white;
    width: 280px;
    max-width:65%;
    height: 100vh;
    z-index:200;
    position: fixed;
    top:0;
    left:0;
    text-align: center;
    box-sizing: border-box;
    transition: transform 0.7s ease-out;
}

.SideDrawer .Logo{
    font-size: 2rem;
    padding: 2rem;
    color: chartreuse;
    border-bottom: solid 2px white;
    font-weight: 900;
}

.SideDrawer .nav_list{
    padding:2rem;
    font-weight: 700;
    display: flex;
    flex-flow: column;
}
.SideDrawer .nav_item{
    padding: 20px 0;
}

a{
    color:white;
    text-decoration: none;
}



.Open{
    transform: translateX(0);
}

.Close{
    transform: translateX(-280px);
}